<template>
  <div class="container">
    <div class="container-form">
      <el-form ref="qryParamRef" :model="qryParam" :rules="paramRules" size="small">
        <el-row :gutter="20">
          <el-col :span="5">
            <el-form-item :label="$t('simMag.addressId')" prop="addressIds">
              <ct-async-select
                v-model="qryParam.addressIds"
                :placeholder="selectPh('simMag.addressId')"
                url="/prodInst/getSupplierListForQuery"
                method="get"
                label-key="attrValueName"
                value-key="attrValue"
              ></ct-async-select>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item :label="$t('traffic.groupId')" prop="accessNumber">
              <el-input
                v-model="qryParam.accessNumber"
                type="number"
                :placeholder="inputPh('traffic.groupId')"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item :label="$t('traffic.billCycle')" prop="billCycle">
              <BillCycleSelect v-model="qryParam.billCycle" />
            </el-form-item>
          </el-col>
          <el-col :span="9">
            <el-form-item label-width="0">
              <div class="search-button-group">
                <el-button
                  type="primary"
                  icon="el-icon-search"
                  @click="loadData(false)"
                  >{{ $t('common.query') }}</el-button
                >
                <el-button icon="el-icon-refresh" @click="reset">{{
                  $t('common.reset')
                }}</el-button>
              </div>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <el-table
        ref="table"
        v-loading="loading"
        class="container-table"
        :data="tableData"
        :border="true"
        height="100%"
        width="100%"
      >
        <el-table-column
          prop="accNum"
          :label="$t('traffic.groupId')"
          show-overflow-tooltip
          resizable
          min-width="110"
        >
          <template slot-scope="scope">
            <a class="cor-blue" @click="toDetail(scope.row)">{{ scope.row.accNum }}</a>
          </template>
        </el-table-column>
        <el-table-column
          prop="useCustName"
          :label="$t('traffic.useCustName')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
        <el-table-column :label="$t('traffic.memberMag')" min-width="110">
          <template slot-scope="scope">
            <a class="cor-blue el-icon-user" @click="toMemberMag(scope.row)"></a>
          </template>
        </el-table-column>
        <el-table-column
          prop="billCycle"
          :label="$t('traffic.billCycle')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
        <el-table-column
          prop="poolTotalValue"
          :label="$t('traffic.usageAvaMonth')"
          show-overflow-tooltip
          min-width="110"
        >
        </el-table-column>
        <el-table-column
          prop="poolUsageValue"
          :label="$t('traffic.usageUsedMonth')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
        <el-table-column
          prop="poolRemainValue"
          :label="$t('traffic.usageRemainMonth')"
          show-overflow-tooltip
          min-width="110"
        >
        </el-table-column>
        <el-table-column
          prop="effDate"
          :label="$t('traffic.effDate')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
        <el-table-column
          prop="expDate"
          :label="$t('traffic.expDate')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
      </el-table>
      <el-pagination
        v-show="tableData.length > 0"
        background
        :current-page="page.pageIndex"
        align="right"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="page.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </div>
  </div>
</template>
<script>
import langMixin from '@/mixins/langMixin.js'
import fetch from '@/request/fetch.js'
import moment from 'moment'
export default {
  mixins: [langMixin],
  data() {
    return {
      loading: false,
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      tableData: [],
      qryParam: {
        billCycle: moment().format('YYYYMM'),
        accessNumber: '', // 群号
        addressIds: '121215042193',
      },
    }
  },
  computed: {
    paramRules() {
      return {
        addressIds: [
          { required: true, trigger: 'blur', message: this.selectPh('simMag.addressId') },
        ],
      }
    },
  },
  mounted() {
    this.loadData(false)
  },
  methods: {
    async loadData(pageChangeFlag) {
      await this.$refs.qryParamRef.validate()
      if (!pageChangeFlag) {
        this.page.pageIndex = 1
        this.page.pageSize = 10
      }
      const param = {
        ...this.qryParam,
        addressIds: [this.qryParam.addressIds],
        pageInfo: this.page,
      }
      this.loading = true
      const res = await fetch('/flowPool/qryForwardFlowPoolList', param, {
        method: 'post',
      }).finally(() => {
        this.loading = false
      })
      res.result?.forEach((item) => (item.billCycle = this.qryParam.billCycle))
      this.tableData = res.result || []
      this.page.total = res.rowCount || 0
      // 列表查询使用量
      this.qryAllFlowUsage()
    },
    async qryAllFlowUsage() {
      const promises = this.tableData.map((row) => {
        const params = {
          prodInstId: row.prodInstId,
          billMonth: this.qryParam.billCycle,
        }
        return this.qrySimMonthFlowUsage(params)
      })
      const totalFlowMBs = await Promise.all(promises)
      totalFlowMBs.forEach((data, index) => {
        this.$set(this.tableData[index], 'poolRemainValue', data.poolRemainValue)
        this.$set(this.tableData[index], 'poolTotalValue', data.poolTotalValue)
        this.$set(this.tableData[index], 'poolUsageValue', data.poolUsageValue)
      })
    },
    async qrySimMonthFlowUsage(params) {
      const res = await fetch('/usageData/queryForwardUsageInfo', params, {
        method: 'get',
      })
      return res
    },
    async toDetail(row) {
      const params = {
        msisdn: row.accNum,
        pageInfo: {
          pageIndex: 1,
          pageSize: 10,
        },
      }
      const res = await fetch('/prodInst/getSimCardPage', params, {
        method: 'post',
      })
      const index = this.tableData.findIndex((item) => item.accNum == row.accNum)

      if (res.result && res.result[index]) {
        const row = res.result[index]
        this.$router.push({
          path: '/infoQuery/simDetail',
          query: {
            row: JSON.stringify(row),
          },
        })
      }
    },
    toMemberMag(row) {
      this.$set(row, 'billMonth', this.qryParam.billCycle)
      this.$router.push({
        path: '/infoQuery/frontMemberMag',
        query: { row: JSON.stringify(row) },
      })
    },
    handleSizeChange(val) {
      this.page.pageSize = val
      this.page.pageIndex = 1
      this.loadData(true)
    },
    handleCurrentChange(val) {
      this.page.pageIndex = val
      this.loadData(true)
    },
    // 重置
    reset() {
      this.$refs.qryParamRef.resetFields()
      this.$nextTick(() => this.loadData(false))
    },
  },
}
</script>
<style lang="scss" scoped>
@import '@/assets/css/page.scss';
</style>
